.Home {
  height: 100%;
  background: url(asset/bg.jpg) center center no-repeat;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}

.Home-logo {
  margin: 0 auto;
  width: 1.06666667rem;
  padding-bottom: 0.53333333rem;
}

.Home-logo > img {
  width: 100%;
}

.Home-slogen {
  margin: 0 auto;
  width: 8rem;
  padding-bottom: 0.53333333rem;
}

.Home-slogen > img {
  width: 100%;
}

.Home-menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
}

.Home-menu > div {
  display: -ms-flexbox;
  display: flex;
  padding-right: 0.8rem;
}

.Home-menu > div > a {
  width: 1.06666667rem;
}

.Home-menu > div > a > img {
  width: 100%;
}

.Home-menu > div:last-child {
  padding-right: 0;
}


